import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import moment from "moment";

// import component
import Button from "../../../../components/uielements/button";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import NumberFormat from "../../../App/NumberFormat";
import PageHeader from "../../../../components/utility/pageHeader";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import notification from "../../../../components/notification";
import { Col, Collapse, Empty, DatePicker, Divider, Row } from "antd";
import { getFileName } from "../../../../helpers/helper";

// import action
import actions from "../../../../redux/report/retainedEarningReport/actions";

import ExcelWrapper from "../../../App/ExcelWrapper";

// print
import ButtonPrint from "../../../App/ButtonPrint";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";

// pdf export
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;

const redirectTo = "/dashboard/report-menus";

class RetainedEarningReport extends Component {
  state = {
    filter: {
      trans_year: moment().startOf("month")
    },
    filterExport: {
      trans_year: moment().format("YYYY")
    },
    open: false,
    search: null
  };

  componentDidMount() {
    // const { getRetainedEarningReports } = this.props;
    // getRetainedEarningReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.retainedEarningReports.error) {
      notification("error", this.props.retainedEarningReports.error);
      this.props.clearError();
    }
    // Display success notif
    if (this.props.retainedEarningReports.success) {
      notification("success", this.props.retainedEarningReports.success);
      this.props.clearSuccess();
    }
  }

  filterValue = () => {
    const { filterExport } = this.state;
    let string = "";

    if (filterExport.trans_year) {
      string
        ? (string += "&trans_year=" + filterExport.trans_year)
        : (string += "trans_year=" + filterExport.trans_year);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getRetainedEarningReports } = this.props;
    let filterExport = this.filterValue();
    this.setState({ search: null });
    getRetainedEarningReports(1, null, filterExport, this.state.filterExport);
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "trans_year") {
      this.setState({
        filter: {
          ...this.state.filter,
          trans_year: value
        },
        filterExport: {
          ...this.state.filterExport,
          trans_year: moment(value).format("YYYY")
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  handleFilterTransYear = e => {
    const { value } = e.target;
    this.setState({
      filter: {
        ...this.state.filter,
        trans_year: value
      }
    });
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      // data.is_active = 1;
      this.props.activateSupplierPriceList(id);
    } else {
      // data.is_active = 0;
      this.props.deactivateSupplierPriceList(id);
    }
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getRetainedEarningReports } = this.props;
    this.setState({
      filter: {
        trans_year: moment().startOf("month")
      },
      filterExport: {
        trans_year: moment().format("YYYY")
      }
    });
    this.setState({ search: null });
    getRetainedEarningReports();
  };

  render() {
    const { retainedEarningReports } = this.props;
    const {
      loadingReport,
      locations,
      retained_earning_reports,
      filter,
      filter_select
    } = retainedEarningReports;
    
    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "month" }),
        dataIndex: "month_name",
        rowKey: "month_name"
      },
      {
        title: `${getLanguage({ id: "amount" })} (IDR)`,
        dataIndex: "amount",
        rowKey: "amount",
        align: "right",
        parse: true,
        render: text => {
          return (
            <span>
              <NumberFormat value={parseFloat(text)} />
            </span>
          );
        }
      }
    ];

    // --- init variable
    const dataSource = [];
    const title = "retainedEarningReports.pageHeader";

    if (retained_earning_reports && retained_earning_reports.length > 0) {
      retained_earning_reports.map((item, index) => {
        let month_name;
        switch (item.month_id) {
          case 1:
            month_name = getLanguage({ id: "january" });
            break;
          case 2:
            month_name = getLanguage({ id: "february" });
            break;
          case 3:
            month_name = getLanguage({ id: "march" });
            break;
          case 4:
            month_name = getLanguage({ id: "april" });
            break;
          case 5:
            month_name = getLanguage({ id: "may" });
            break;
          case 6:
            month_name = getLanguage({ id: "june" });
            break;
          case 7:
            month_name = getLanguage({ id: "july" });
            break;
          case 8:
            month_name = getLanguage({ id: "august" });
            break;
          case 9:
            month_name = getLanguage({ id: "september" });
            break;
          case 10:
            month_name = getLanguage({ id: "october" });
            break;
          case 11:
            month_name = getLanguage({ id: "november" });
            break;
          case 12:
            month_name = getLanguage({ id: "december" });
            break;
        }
        dataSource.push({
          ...retained_earning_reports[index],
          month_name,
          key: index
        });
      });
    }

    // --- ambil data user dari state global:auth
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //const { current_page, page_size, total } = this.props.accountHistory;
    const fileName = getLanguage({ id: "retainedEarningReports.pageHeader" })

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="retainedEarningReports.pageHeader" />}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              columns={columns}
              data={dataSource}
              dateFormat="YYYY"
              filter={filter_select}
              filterDate="trans_year"
              locations={locations}
              title={<IntlMessages id="retainedEarningReports.pageHeader" />}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loadingReport} />}
              content={() => this.componentRef}
              oonBeforePrint={
                () => (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => document.title = siteConfig.documentTitle}
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loadingReport}
              title={title}
            />

            <ExcelWrapper
              title={title}
              fileName={fileName}
              loading={loadingReport}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              filterDate="trans_year"
              dateFormat="YYYY"
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {/* Filter */}
          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="start"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={6}>
                    <label>
                      <IntlMessages id="retainedEarningReports.filter.transYear" />
                    </label>
                    <br />
                    <DatePicker
                      format="YYYY"
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={this.state.filter.trans_year}
                      open={this.state.open}
                      mode="year"
                      onOpenChange={status =>
                        status ? this.setState({ open: status }) : null
                      }
                      onPanelChange={value => {
                        this.handleFilterChange("trans_year", value);
                        this.setState({ open: false });
                      }}
                    />
                  </Col>
                  <Col
                    span={6}
                    style={{
                      marginTop: "24px"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          {/* /Filter */}

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="details" />
          </Divider>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : !loadingReport && dataSource.length === 0 ? (
              <Empty
                description={
                  <IntlMessages id="retainedEarningReports.table.noData" />
                }
              />
            ) : (
                  <div className="isoInvoiceTable">
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        bordered={true}
                        columns={columns}
                        className="invoiceListTable"
                        dataSource={dataSource}
                        loading={loadingReport}
                        pagination={false}
                        rowKey={record => record.key}
                      />
                    </Scrollbars>
                  </div>
                )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    retainedEarningReports: state.retainedEarningReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(RetainedEarningReport);
