const actions = {
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  getRetainedEarningReports: (page, search, filter, filter_select) => ({
    type: "GET_RETAINED_EARNING_REPORTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter,
      filter_select
    }
  })
};

export default actions;
